import { mapState,mapGetters } from 'vuex';
import headTodo from './headTodo.vue'
import peopleList from './peopleList.vue'
import plan from './plan.vue'
import errManage from './errManage.vue'
import permision from "@/utils/permission.js"
export default {
	data() {
		return {
			is_unfold: false,
			list: [
				{
					name: '李小吧',
					age: '22',
					tag: ['电工学徒'],
					address: '深圳市龙华区大浪街',
					status: '准备中',
					status_no: 2
				},
				{
					name: '李小吧',
					age: '22',
					tag: ['电工学徒'],
					address: '深圳市龙华区大浪街',
					status: '确认能来',
					status_no: 1
				},
				{
					name: '李小吧',
					age: '22',
					tag: ['电工学徒'],
					address: '深圳市龙华区大浪街',
					status: '确认不来',
					status_no: 0
				},
				{
					name: '李小吧',
					age: '22',
					tag: ['电工学徒'],
					address: '深圳市龙华区大浪街',
					status: '等待确认',
					status_no: 3
				}
			],
			swiperHeight:'',
			project_detail:{},
			downOption: {
				auto: false //是否在初始化后,自动执行下拉回调callback; 默认true
			},
			upOption: {
				auto: false,
				isLock:true,
			},
			current_swiper:0,
			form_data:{
				startTime:'',
				address:'',
				points:'',
				description:''
			},
			tab_list:[
				{
					name:'待办事项',
					data:[],
					is_rquest:false
				},
				{
					name:'人员列表',
					data:[],
					is_rquest:false
				},
				{
					name:'生产计划',
					data:[],
					is_rquest:false
				},
				{
					name:'异常管理',
					data:[],
					is_rquest:false
				},
			],
			TabCur:0,
			scrollLeft:0,
			no_work:false
		};
	},
	props: {
		location:{
			default:null
		}
	},
	components:{
		headTodo,
		peopleList,
		plan,
		errManage
	},
	mounted() {
		uni.$on('refreshJwt',(data) =>{
			this.init()
		})
		uni.$on('taskToWork',(data) =>{
			this.init()
		})
		this.init()
	},
	beforeDestroy() {
		uni.$off('refreshJwt')
		uni.$off('taskToWork')
	},
	computed:{
		...mapState(['bgt_c_task','bgt_ct_id']),
		...mapGetters(['user_role','id']),
		person_list(){
			let arr = this.$utils._get(this.project_detail,'personList',[])
			arr.forEach(item =>{
				this.$set(item,'custom_wages','')
			})
			return arr
		},
		project(){
			return this.$utils._get(this.project_detail,'project',{})
		},
		c_CustomBar(){
			return this.CustomBar
		},
		current_bgt_c_task(){
			return this.bgt_c_task.find((item) =>{
				return item.id == this.bgt_ct_id
			})
		}
	},
	watch:{
		bgt_ct_id(val){
			console.log(val)
			this.current_swiper = this.bgt_c_task.findIndex((item) =>{
				return item.id == val
			})
			this.init()
		},
		location:{
			handler(val){
				console.log('wlocation',val)
				if(val){
					this.form_data.address = val.address
					this.form_data.points = `[${val.latitude},${val.longitude}]`
					console.log(this.form_data)
				}
			},
			deep:true
		}
	},
	methods: {
		init(){
			console.log(this.bgt_c_task,this.bgt_ct_id)
			this.get_head_project()
			// if(this.bgt_ct_id == ''){
				
			// }else{
			// 	this.get_project_detail()
			// }
		},
		set_form_data(){
			let index = this.bgt_c_task.findIndex((item) =>{
				return item.id == this.bgt_ct_id
			})
			this.current_swiper = index
			this.form_data.startTime = this.$utils.format_date(this.$_.get(this.bgt_c_task[index],'startTime',''))
			this.form_data.address = this.$utils._get(this.bgt_c_task[index],'address','')
			this.form_data.points = this.$utils._get(this.bgt_c_task[index],'points','')
			this.form_data.description = this.bgt_c_task[index].description
		},
		get_head_project(cb){
			this.$http.get('personwx.headproject/1.0/',{
				pid:this.id,
				isFinish:'0'
			}).then(res =>{
				console.log(res)
				if(this.$utils._get(res,'data.code') === '0'){
					if(res.data.data.length == 0){
						this.no_work = true
						return
					}else{
						this.no_work = false
					}
					this.$store.commit('set_bgt_c_task',this.$utils._get(res,'data.data',[]))
					if(this.bgt_ct_id == ''){
						this.$store.commit('set_bgt_ct_id',this.$utils._get(res,'data.data[0].id',''))
					}
					this.get_project_detail(cb)
				}
			})
		},
		get_project_detail(cb){
			this.set_form_data()
			this.$http.get('personwx.projectDetail/1.0/',{
				id:this.bgt_ct_id
			}).then(res =>{
				if(this.$utils._get(res,'data.code') === '0'){
					console.log(res)
					this.project_detail = this.$utils._get(res,'data.data',{})
					if(typeof cb === 'function'){
						cb()
					}
					this.$nextTick(() =>{
						this.get_swiper_c_height()
					})
				}
				
			})
		},
		get_swiper_c_height(){
			const query = uni.createSelectorQuery().in(this);
			query.select('#content'+this.current_swiper).boundingClientRect(data => {
			  console.log("得到布局位置信息" + JSON.stringify(data));
			  console.log("节点离页面顶部的距离为" + data.top);
			  this.swiperHeight = data.height+'px'
			}).exec();
		},
		status_class(status_no) {
			if (status_no === 1) {
				return 'text-green';
			} else if (status_no === 2) {
				return 'text-blue';
			} else if (status_no === 3) {
				return 'text-orange';
			} else {
				return 'text-grey';
			}
		},
		status_text(status_no){
			if (status_no === 1) {
				return '确认能来';
			} else if (status_no === 2) {
				return '准备中';
			} else if (status_no === 3) {
				return '等待确认';
			} else {
				return '确认不来';
			}
		},
		expectedPlace(addr){
			let address = ''
			try{
				address = JSON.parse(addr).join('')
			}catch(e){
				//TODO handle the exception
			}
			return address
		},
		goto_detail(item) {
			uni.navigateTo({
				url: `/pages/auditDetail/auditDetail?id=${this.project.id}&pid=${item.id}&isWork=true` 
			});
		},
		deliveryPeriod(time){
			return this.$utils.format_date(time)
		},
		case_more(){
			this.is_unfold = !this.is_unfold
			this.$nextTick(() =>{
				this.get_swiper_c_height()
			})
			
		},
		downCallback(mescroll) {
			// 这里加载你想下拉刷新的数据, 比如刷新轮播数据
			// loadSwiper();
			// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
			mescroll.resetUpScroll();
		},
		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
		upCallback(mescroll) {
			//联网加载数据
			setTimeout(() => {
				this.get_head_project(() =>{
					mescroll.endSuccess(this.person_list.length,false)
				})
			}, 300);
		},
		goto_recruiting(){
			this.show_modal = false
			uni.navigateTo({
				url: `/pages/personList/personList?recId=${this.project.id}&proId=${this.project.pid}`
			});
		},
		swiperChange(e){
			console.log(e)
			this.current_swiper = e.detail.current
			let id = this.$utils._get(this.bgt_c_task[this.current_swiper],'id','')
			this.$store.commit('set_bgt_ct_id',id)
			// this.$nextTick(() =>{
			// 	this.get_project_detail()
			// })
		},
		before_send_start(){
			if(this.$_.every(this.person_list,o =>{
				return o.custom_wages !== ''
			})){
				uni.showLoading({
				    title: '加载中',
					mask:true
				});
				let WageDate = []
				this.person_list.forEach(item =>{
					WageDate.push({
						id:item.id,
						value:item.custom_wages
					})
				})
				this.$http.post('personwx.updatePersonWages/1.0/',{
					WageDate: JSON.stringify(WageDate)
				}).then(res =>{
					uni.hideLoading();
					this.send_start()
				})
			}else{
				uni.showToast({
				    title: '请输入每位人员工价',
				    duration: 2000,
					icon:'none'
				});
				console.log(this.person_list)
			}
		},
		send_start(){
			uni.showModal({
			    title: '提示',
			    content: '确认输入的开工信息是否有误',
				confirmText:'确认开工',
			    success:(res) => {
			        if (res.confirm) {
			            console.log('用户点击确定');
						this.$http.post('personwx.updateprojectinfo/1.0/',{
							startTime:this.form_data.startTime,
							address:this.form_data.address,
							points:this.form_data.points,
							description:this.form_data.description,
							id:this.bgt_ct_id
						}).then(res =>{
							console.log(res)
							let params ={
								id:this.bgt_ct_id,
								address:this.form_data.address,
								points:this.form_data.points,
								startTime: this.form_data.startTime
							}
							console.log(params)
							this.$http.post('personwx.startproject/1.0/',params).then(res =>{
								console.log(res)
								this.get_head_project()
							})
							
						})
						
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
			
		},
		dpDateChange(e){
			this.form_data.startTime = e.detail.value
		},
		chooseLocation(){
			const key = 'CLABZ-JECLX-JDZ43-TAZKO-FNNM7-NVF2G'; //使用在腾讯位置服务申请的key
			const referer = '智造劳务'; //调用插件的app的名称
			let location
			if(this.form_data.points == ''){
				location = ''
			}else{
				const loctaionp = JSON.parse(this.form_data.points)
				location = JSON.stringify({
				  latitude: loctaionp[0],
				  longitude: loctaionp[1]
				});
			}
			
			wx.navigateTo({
			  url: `plugin://chooseLocation/index?key=${key}&referer=${referer}&location=${location}`
			});
		},
		tabSelect(e){
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		goto_log(){
			uni.navigateTo({
				url:'/pages/tabbar/workbench/logReport/logReport'
			})
		},
		goto_error(){
			uni.navigateTo({
				url:'/pages/tabbar/workbench/errorReport/errorReport'
			})
		},
		goto_rating(){
			uni.navigateTo({
				url:'/pages/tabbar/workbench/ratingList/ratingList'
			})
		},
		async scan(){
			let status = await this.checkPermission();
			if (status !== 1) {
				return;
			}
			uni.scanCode({
				success: res => {
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
				},
				fail: err => {
					console.log(err);
				}
			});
		},
		async checkPermission(code) {
			let status = permision.isIOS ? await permision.requestIOS('camera') : await permision.requestAndroid(
				'android.permission.CAMERA');
		
			if (status === null || status === 1) {
				status = 1;
			} else {
				uni.showModal({
					content: '需要相机权限',
					confirmText: '设置',
					success: function(res) {
						if (res.confirm) {
							permision.gotoAppSetting();
						}
					}
				});
			}
			return status;
		},
		finish_task(){
			uni.showModal({
			    title: '提示',
			    content: '辛苦了，确认任务结案？',
				'confirmText':'确认结案',
			    success:(res) => {
			        if (res.confirm) {
			            console.log('用户点击确定');
						this.$http.post('personwx.projectfinish/1.0/',{
							id:this.bgt_ct_id
						}).then(res =>{
							console.log(res)
							if(this.$_.get(res,'data.code','') == '0'){
								uni.showToast({
								    title: '任务结案成功',
								    duration: 2000,
									icon:'success',
									success:() =>{
										uni.$emit('refreshList')
										this.init()
										setTimeout(() => {
											uni.switchTab({
												url:"/pages/tabbar/task/task"
											})
										}, 300);
										
									}
								})
							}else{
								uni.showToast({
								    title: '任务结案失败',
								    duration: 2000,
								});
							}
						})
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
			
		},
		goto_work(){
			uni.switchTab({
				url:"/pages/tabbar/task/task"
			})
		}
	}
};
